.btn {
  padding: 20px;

  .btn-container {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .shuffle-arrow {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .arrows {
    width: 5.9vw;
    padding: 10px;
    cursor: pointer;
    margin: 20px 50px;
    border: 4px solid white;
    border-radius: 50%;
    background-image: linear-gradient(to left,
        #51e2f5,
        #9df9ef,
        #edf756,
        #ffa8b6,
        #a28089);
    background-clip: content-box;
  }
}

@media screen and (max-width: 870px) {
  .btn{
    .arrows{
      margin: 10px;
      width: 15vw;
    }
  }
}